import http from '@/api/http'
import { RequestApiFn } from '@/api/types'

// 登录
export const signIn: RequestApiFn = (data, config) => {
  const url: string = '/users/signIn'
  return http.post(url, data, config)
}

// 注册
export const signUp: RequestApiFn = (data, config) => {
  const url: string = '/users/signUp'
  return http.post(url, data, config)
}

// 获取用户信息
export const getUserInfo: RequestApiFn = (data, config) => {
  const url: string = '/users/getUserInfo'
  return http.post(url, data, config)
}

// 退出登录
export const signOut: RequestApiFn = (data, config) => {
  const url: string = '/users/signOut'
  return http.post(url, data, config)
}
