import api from '@/api'
import * as types from '@/store/actionTypes'
import { Action, Dispatch, UserInfo, Token } from '@/store/types'
import { ResponseData, RequestData } from '@/api/types'

// 设置用户信息
export function setUserInfo(userInfo: UserInfo): Action {
  return {
    type: types.setUserInfo,
    payload: { userInfo }
  }
}

// 清除token和用户信息
export function signOutLocal(token: Token = null): Action {
  return {
    type: types.signOutLocal,
    payload: {
      token,
      userInfo: {}
    }
  }
}

// 登录
export function signIn(params: RequestData): any {
  return (dispatch: Dispatch): Promise<ResponseData> => {
    return api.signIn(params).then(({ success, data, ...args }) => {
      if (success) dispatch(setUserInfo(data))
      return {
        data,
        success,
        ...args
      }
    })
  }
}

// 注册
export function signUp(params: RequestData): any {
  return (dispatch: Dispatch): Promise<ResponseData> => {
    return api.signUp(params).then(({ success, data, ...args }) => {
      if (success) dispatch(setUserInfo(data))
      return {
        data,
        success,
        ...args
      }
    })
  }
}

// 注销
export function signOut(params?: RequestData): any {
  return (dispatch: Dispatch): Promise<ResponseData> => {
    return api.signOut(params).then(({ success, data, ...args }) => {
      if (success) dispatch(signOutLocal(data))
      return {
        data,
        success,
        ...args
      }
    })
  }
}

// 获取用户信息
export function getUserInfo(params?: RequestData) {
  return (dispatch: Dispatch): Promise<ResponseData> => {
    return api.getUserInfo(params).then(({ success, data, ...args }) => {
      if (success) dispatch(setUserInfo(data))
      return {
        data,
        success,
        ...args
      }
    })
  }
}
