import JsEncrypt from 'jsencrypt'
import config from '@/config/config'

/**
 * @description 加密字段
 *
 * @param {String} str 需要加密的文本
 * @return {String}
 */
export function encrypted(str: string): string {
  // 新建JsEncrypt对象
  const enCrypt = new JsEncrypt()
  // 设置公钥
  enCrypt.setPublicKey(config.publicKey)
  // 加密数据
  return enCrypt.encrypt(str)
}

/**
 * @description 字符串排序后倒叙
 *
 * @param {String} str
 * @return {String}
 */
export function reverseString(str: string): string {
  return str.split('').sort().reverse().join('')
}

/**
 * @description 生成指定位数随机数
 *
 * @param {Number} count 生成位数
 * @return {String}
 */
export function randomStr(count: number = 6): string {
  const random = Math.floor(Math.random() * +(1 + '0'.repeat(count)))
  return random.toString()
}

/**
 * @description 判断参数是否为对象
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isObject(target: any): boolean {
  return Object.prototype.toString.call(target) === '[object Object]'
}

/**
 * 获取svg图标(id)列表
 *
 * @return {Array} list 全部svg-icon的id数组
 */
export function getSvgIdList(): string[] {
  const symbols = document.querySelectorAll('svg>symbol')
  return Array.from(symbols).map(item => item.id)
}
