import http from '@/api/http'
import { RequestApiFn } from '@/api/types'

let getWebTokenResult: Promise<string | null> | null = null

// 获取token
export const getWebToken: RequestApiFn<string | null> = (data, config) => {
  data = {
    _isNeedToken: false,
    _isNeedLoading: false,
    ...data
  }
  const url: string = '/common/getWebToken'
  getWebTokenResult = getWebTokenResult || new Promise(resolve => {
    http.post(url, data, config).then(({ success, data }) => {
      resolve(success ? data : null)
      getWebTokenResult = null
    }).catch(() => {
      resolve(null)
      getWebTokenResult = null
    })
  })
  return getWebTokenResult
}

