import * as types from './actionTypes'
import { Reducer } from 'redux'
import { State, Action } from '@/store/types'

const reducer: Reducer<State, Action> = (state: State | undefined, action: Action): State => {
  let key
  for (key in types) {
    if (key === action.type && Object.prototype.hasOwnProperty.call(types, key)) {
      return {
        ...<State>state,
        ...action.payload
      }
    }
  }
  return <State>state
}

export default reducer
