import api from '@/api'
import * as types from '@/store/actionTypes'
import {
  State,
  Action,
  Dispatch,
  Token,
  Locale,
  MenuItem,
  ComponentSize
} from '@/store/types'

/**
 *
 * 同步Action
 *
 * */

// 设置token
export function setWebToken(token: Token): Action {
  return {
    type: types.setWebToken,
    payload: { token }
  }
}

// 设置语言
export function setLocale(locale: Locale): Action {
  return {
    type: types.setLocale,
    payload: { locale }
  }
}

// 设置全局组件大小
export function setComponentSize(componentSize: ComponentSize): Action {
  return {
    type: types.setComponentSize,
    payload: { componentSize }
  }
}

// 设置菜单
export function setMenus(menus: MenuItem[]): Action {
  return {
    type: types.setLocale,
    payload: { menus }
  }
}

/**
 *
 * 异步Action
 *
 * */

// 获取token
export function getWebToken(dispatch: Dispatch, getState: () => State): Promise<string | null> {
  const token = getState().token
  if (token) return Promise.resolve(token)
  return api.getWebToken().then((token) => {
    dispatch(setWebToken(token))
    return token
  })
}
