import React, { useEffect } from 'react'
import Router from '@/router'
import { ConfigProvider } from 'antd'
import { IntlProvider } from 'react-intl'
import { useDispatch, useSelector } from 'react-redux'
import { State, Locale, ComponentSize } from '@/store/types'
import { getUserInfo } from '@/store/actions/user'
import antdEnUS from 'antd/es/locale/en_US'
import antdZhCN from 'antd/es/locale/zh_CN'
import antdZhTW from 'antd/es/locale/zh_TW'
import zhCN from '@/locales/zh_CN'
import zhTW from '@/locales/zh_TW'
import enUS from '@/locales/en_US'

function App() {
  const dispatch = useDispatch()
  const locale = useSelector<State, Locale>(state => state.locale)
  const loginStatus = useSelector<State, boolean>(state => !!state.userInfo.username)
  const componentSize = useSelector<State, ComponentSize>(state => state.componentSize)
  const locales: { [key: string]: any } = {
    'zh-CN': zhCN,
    'zh-TW': zhTW,
    'en-US': enUS
  }
  const antdLocales: { [key: string]: any } = {
    'zh-CN': antdZhCN,
    'zh-TW': antdZhTW,
    'en-US': antdEnUS
  }

  useEffect(componentDidMount, [])

  function componentDidMount() {
    loginStatus && dispatch(getUserInfo())
  }

  function onError(e: any) {
    console.log(e)
  }

  return (
    <ConfigProvider componentSize={componentSize} locale={antdLocales[locale]}>
      <IntlProvider onError={onError} locale={locale} messages={locales[locale]}>
        <Router/>
      </IntlProvider>
    </ConfigProvider>
  )
}

export default App
