import qs from 'qs'
import md5 from 'js-md5'
import axios from 'axios'
import store from '@/store/index'
import config from '@/config/config.js'
import { State } from '@/store/types'
import {
  RequestData,
  ResponseData,
  RequestMethod,
  RequestSetting,
  AxiosRequestConfig
} from '@/api/types'
import { reverseString, randomStr } from '@/utils/util'
import { getWebToken } from '@/store/actions/common'
import { signOutLocal } from '@/store/actions/user'
import { message as antMessage, Modal } from 'antd'
import { history } from '@/router'

let modal: { update: any; destroy?: () => void }
const locales: { [key: string]: any } = {
  'zh-CN': {
    tips: '提示',
    confirm: '确定'
  },
  'zh-TW': {
    tips: '提示',
    confirm: '確定'
  },
  'en-US': {
    tips: 'Tips',
    confirm: 'OK'
  }
}
// 请求中的qid列表
let qids: string[] = []

// 创建 axios 实例
const _http = axios.create({
  timeout: 30 * 1000,
  baseURL: config.apiUrl,
  headers: {
    AppId: config.appId
  }
})

function setHeader(config: AxiosRequestConfig, isNeedToken: boolean, isNeedLoading: boolean): Promise<AxiosRequestConfig> {
  return getToken(isNeedToken).then(token => {
    const qid = md5(Date.now() + randomStr(6))
    const dataStr = reverseString(JSON.stringify(config.data))
    const headers = {
      Qid: qid,
      Token: token,
      Sign: md5(qid + dataStr + token)
    }
    if (isNeedLoading) {
      qids.push(qid)
      console.log('loading.open')
    }
    Object.assign(config.headers, headers)
    return config
  })

  function getToken(isNeedToken: boolean): Promise<string | null> {
    if (isNeedToken) {
      return store.dispatch<any>(getWebToken)
    } else {
      return Promise.resolve(store.getState().token)
    }
  }
}

// 添加请求拦截器
_http.interceptors.request.use(config => {
  const state: State = store.getState()
  const data: RequestData = {
    ...config.data
  }
  const isNeedToken: boolean = data._isNeedToken !== false
  const isNeedLoading: boolean = data._isNeedLoading !== false
  const isNeedLoginName: boolean = data._isNeedLoginName !== false
  const isShowErrorTips: boolean = data._isShowErrorTips !== false
  const notShowErrorTipsCode: string = data._notShowErrorTipsCode || '0'
  const httpAgent: RequestSetting = {
    isNeedLoading,
    isShowErrorTips,
    notShowErrorTipsCode
  }
  const urlencoded = new RegExp('application/x-www-form-urlencoded').test(config.headers['Content-Type'])

  delete data._isNeedToken
  delete data._isNeedLoading
  delete data._isNeedLoginName
  delete data._isShowErrorTips
  delete data._notShowErrorTipsCode

  if (isNeedLoginName && !data.loginName) {
    data.loginName = state.userInfo.username
  }

  config.data = urlencoded ? qs.stringify(data) : data
  config.httpAgent = httpAgent
  return setHeader(config, isNeedToken, isNeedLoading)
}, error => {
  console.log('request error:\n', error)
  return Promise.reject(error)
})

// 添加响应拦截器
_http.interceptors.response.use((response) => {
  const state: State = store.getState()
  const locale = locales[state.locale]
  const data: ResponseData = response.data
  const code: string = data.code
  const message: string = data.message
  const qid: string = response.config.headers.Qid
  const httpAgent: RequestSetting = response.config.httpAgent
  const notTipsCode: Array<string> = ['0']
  const needLoginCode: Array<string> = ['N_000003', 'N_000004', 'N_000015']
  const isTips: boolean = httpAgent.isShowErrorTips && code !== httpAgent.notShowErrorTipsCode && !~notTipsCode.indexOf(code)

  data.success = code === '0'
  qids = qids.filter(item => item !== qid)

  if (!qids.length) console.log('loading.hide')

  if (~needLoginCode.indexOf(code)) {
    if (modal) {
      modal.update({
        title: locale.tips,
        okText: locale.confirm,
        content: message
      })
    } else {
      modal = Modal.info({
        centered: true,
        keyboard: false,
        title: locale.tips,
        okText: locale.confirm,
        content: message,
        onOk() {
          store.dispatch(signOutLocal())
          history.push('/user/login')
        }
      })
    }
  } else if (isTips) {
    antMessage.error(message)
  }

  return response
}, (error) => {
  console.log('response error:\n', error)
  return Promise.reject(error)
})

const _get: RequestMethod = (url, data, config) => {
  config = {
    params: data,
    ...config
  }
  return _http.get(url, config).then(({ data }) => <ResponseData>data)
}

const _put: RequestMethod = (url, data, config) => {
  return _http.put(url, data, config).then(({ data }) => <ResponseData>data)
}

const _post: RequestMethod = (url, data, config) => {
  return _http.post(url, data, config).then(({ data }) => <ResponseData>data)
}

const _delete: RequestMethod = (url, data, config) => {
  config = {
    params: data,
    ...config
  }
  return _http.delete(url, config).then(({ data }) => <ResponseData>data)
}

// 封装常用请求方法
const http = {
  get: _get,
  put: _put,
  post: _post,
  delete: _delete
}

export default http
