import { Store } from 'redux'
import { Action, State } from '@/store/types'
import { isObject } from '@/utils/util'

const state: State = {
  token: null, // base64格式的token数据
  locale: 'zh-CN', // 语言类型
  userInfo: {}, // 用户信息
  menus: [], // 菜单
  componentSize: 'middle'
}

export function getState(state: State): State {
  let cacheState: any = window.localStorage.getItem('redux_state')
  cacheState = cacheState && JSON.parse(cacheState)
  cacheState = isObject(cacheState) ? cacheState : {}

  return {
    ...state,
    ...cacheState
  }
}

export function mapState(store: Store<State, Action>): void {
  const cacheState: { [key: string]: any } = {}
  const mapStateKey: string[] = [
    'token',
    'menus',
    'locale',
    'userInfo'
  ]
  const state: any = store.getState()
  mapStateKey.forEach((key) => (cacheState[key] = state[key]))
  window.localStorage.setItem('redux_state', JSON.stringify(cacheState))
}

export default getState(state)
