import React, { lazy, Suspense, useEffect, useState } from 'react'
import { createBrowserHistory } from 'history'
import { Switch, Router, Route, Redirect } from 'react-router'
import config from '@/config/config'
import PageLoading from '@/components/PageLoading'
import { useSelector } from 'react-redux'
import { MenuItem, State } from '@/store/types'

interface RouteProps {
  path: string,
  roleAuth?: boolean, // 是否需要角色权限，值不为false都表示需要
  loginAuth?: boolean, // 是否需要登录权限，值不为false都表示需要
  exact?: boolean,
  strict?: boolean,
  redirect?: string,
  component?: React.ReactNode
  children?: RouteProps[]
}

const routes: RouteProps[] = [
  {
    path: '/user',
    roleAuth: false,
    loginAuth: false,
    component: lazy(() => import(/* webpackChunkName: "user" */'@/views/user/index')),
    children: [
      {
        path: '/user',
        exact: true,
        redirect: '/user/login'
      },
      {
        path: '/user/login',
        exact: true,
        roleAuth: false,
        loginAuth: false,
        component: lazy(() => import(/* webpackChunkName: "login" */'@/views/user/login'))
      },
      {
        path: '/user/register',
        exact: true,
        roleAuth: false,
        loginAuth: false,
        component: lazy(() => import(/* webpackChunkName: "register" */'@/views/user/register'))
      },
      {
        path: '*',
        roleAuth: false,
        loginAuth: false,
        component: lazy(() => import(/* webpackChunkName: "page404" */'@/components/page404'))
      }
    ]
  },
  {
    path: '/',
    roleAuth: false,
    component: lazy(() => import(/* webpackChunkName: "layout" */'@/components/Layout/index')),
    children: [
      {
        path: '/',
        exact: true,
        roleAuth: false,
        redirect: '/menus'
      },
      {
        path: '/users',
        exact: true,
        component: lazy(() => import(/* webpackChunkName: "menu" */'@/views/users'))
      },
      {
        path: '/menus',
        exact: true,
        component: lazy(() => import(/* webpackChunkName: "menu" */'@/views/menus'))
      },
      {
        path: '/roles',
        exact: true,
        component: lazy(() => import(/* webpackChunkName: "roles" */'@/views/roles'))
      },
      {
        path: '/projects',
        exact: true,
        component: lazy(() => import(/* webpackChunkName: "projects" */'@/views/projects'))
      },
      {
        path: '/compile',
        exact: true,
        component: lazy(() => import(/* webpackChunkName: "compile" */'@/views/compile'))
      },
      {
        path: '*',
        roleAuth: false,
        component: lazy(() => import(/* webpackChunkName: "page404" */'@/components/page404'))
      }
    ]
  }
]

const history = createBrowserHistory({
  basename: process.env.NODE_ENV === 'production' ? config.outputDir : ''
})

const RouteWithSubRoutes: React.FC<RouteProps> = (route) => {
  const loginStatus = useSelector<State, boolean>(state => !!state.userInfo.username)
  const { loginAuth, children, component } = route
  const Component = component as React.ElementType

  function render(): React.ReactNode {
    if (loginAuth !== false && !loginStatus) {
      return <Redirect to='/user/login' exact={route.exact} from={route.path}/>
    } else if (new RegExp('^/user$|^/user/').test(route.path) && loginStatus) {
      return <Redirect to='/' exact={route.exact} from={route.path}/>
    }
    return <Component>
      {
        children &&
        <Suspense fallback={<PageLoading/>}>
          <Switch>
            {children.map((route: RouteProps, i: number) => (
              <RouteWithSubRoutes key={i} {...route} />
            ))}
          </Switch>
        </Suspense>
      }
    </Component>
  }

  return route.redirect
    ? <Redirect
      strict={route.strict}
      exact={route.exact}
      from={route.path}
      to={route.redirect}/>
    : <Route
      path={route.path}
      strict={route.strict}
      exact={route.exact}
      render={render}
    />
}

const Routes: React.FC = () => {
  const menus = useSelector<State, MenuItem[] | undefined>(state => state.userInfo.menus)
  const [roleRoutes, setRoleRoutes] = useState<RouteProps[]>([])
  useEffect(() => {
    const menuList = deepFlatten(menus, [])
    const routeList = filterRoutes(routes, [])
    setRoleRoutes(routeList)

    // 剔除用户角色下没有的菜单
    function filterRoutes(routes: RouteProps[], list: RouteProps[] = []): RouteProps[] {
      routes.forEach(route => {
        route = { ...route }
        const isExist = route.roleAuth === false || menuList.some(menu => menu.path === route.path)
        if (isExist) {
          route.children = route.children && filterRoutes(route.children)
          list.push(route)
        }
      })
      return list
    }

    function deepFlatten(menus: MenuItem[] = [], list: any[] = []): any[] {
      menus.forEach(item => {
        list.push({
          id: item.id,
          path: item.path
        })
        item.children.length && deepFlatten(item.children, list)
      })
      return list
    }
  }, [menus])

  return (
    <Router history={history}>
      <Suspense fallback={<PageLoading/>}>
        <Switch>
          {
            roleRoutes.map((route, i) => (
              <RouteWithSubRoutes key={i} {...route} />
            ))
          }
        </Switch>
      </Suspense>
    </Router>
  )
}

